import { Spinner } from "reactstrap";
import { useSelector } from "react-redux";
import {  LOADING } from "../../helper/constants";

export default function Loader() {

    const { contactStore, companyStore,pictureSubmitStore,commentSubmitStore } = useSelector((state:any)=>state);
    const loading:boolean =  contactStore.phase === LOADING || companyStore.phase === LOADING || pictureSubmitStore.phase === LOADING || commentSubmitStore.phase === LOADING 
    return (
        <>
        {
        loading &&
        <Spinner size="" color="warning" type="border" className="loading"> Loading...</Spinner>
        }
        </>
    )
}
