import React, { useEffect } from 'react';
import { NavLink } from 'react-router-dom';
import { CardFooter, Row, Col, Nav, Input, Button } from 'reactstrap';
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from 'react-redux';
import { INIT, LOADING,BASE_URL, STORES } from '../../helper/constants';
import { IContactFromProps } from '../../interfaces/IContactFormProps';
import validator from "validator";
import { contact } from '../../redux/contact/actions';
import { AlertMessage } from '../AlertMessage';
import CookieConsent from "react-cookie-consent";

export default function Footer() { 
    const [t] = useTranslation('common');
    const dispatch = useDispatch();
    const { useState } = React;

    const settingStore = useSelector((state: any) => state.homeStore);
    const { alldata,phase } = settingStore;

    const [formData, setFormData] = useState<IContactFromProps>({
        email: '',
    });

    const [disableContact, setDisableContact] = useState(false);

    const [emailError, setEmailError] = useState<boolean | null>(null)
    const validateEmail = (e: any) => {
        var email = e.target.value

        if (!email) {
            setEmailError(false)
        } else {

            if (validator.isEmail(email)) {
                setEmailError(true);

            } else {
                setEmailError(false);
            }
        }
    }

    const handleChange = (e: any) => {
        const name = e.target.name;
        const value = e.target.value;
        setFormData({ ...formData, [name]: value });
    };

    const handleContact = (e: any) => {

        const { email } = formData;
        if (email && emailError) 
        {
            dispatch(contact(formData));
            
        } else {

            if (!email || !emailError) {
                setEmailError(false);
            }

        }
    }


  useEffect(() => {
 
      if (phase === INIT) {
        setDisableContact(false);
      } else if (phase === LOADING) {
        setDisableContact(true);
      }

  }, [ setDisableContact, phase])
    


    return (
        <>
        <div className="footer">
            <div className="container">
                <Row>
                    <Col sm="4">
                        <CardFooter className="text-muted">
                        <NavLink to="/" reloadDocument>
                            <img src={BASE_URL+'/logo.png'} alt='preveri' />
                            </NavLink>
                            <p dangerouslySetInnerHTML={{ __html: alldata?.setting?.short_desc_about }}></p>
                        </CardFooter>
                    </Col>
                    <Col sm="4">
                        <h3>
                            {t('home_page.short_links')}
                        </h3>
                        <Nav vertical>
                            {/* <NavLink to="#">
                                {t('home_page.jobs')}
                            </NavLink> */}
                            <NavLink to="o-nas">
                                {t('home_page.about_us')}
                            </NavLink>
                            <NavLink to="pogoji-uporabe">
                                {t('home_page.term_of_service')}
                            </NavLink>
                            <NavLink to="politika-zasebnosti">
                                {t('home_page.privacy_policy')}
                            </NavLink>
                            <NavLink to="piskotki">
                                {t('home_page.cookies')}
                            </NavLink>
                        </Nav>
                    </Col>
                    <Col sm="4">
                        <h3>
                            {t('home_page.contact_us')}
                        </h3>
                        <p dangerouslySetInnerHTML={{ __html: alldata?.setting?.short_description }}></p>
                        <AlertMessage store={STORES.CONTACT_STORE} />
                        <Input
                            id="exampleEmail"
                            name="email"
                            placeholder={t('login_form.enter_your_email')}
                            value={formData.email}
                            type="email"
                            onChange={e => { handleChange(e); validateEmail(e) }}
                            valid={emailError === true}
                            invalid={emailError !== null && emailError === false} />

                        <Button color="warning" onClick={handleContact}>
                            {t('register_form.submit')}
                        </Button>

                    </Col>
                </Row>
            </div>
        </div>
        <CookieConsent buttonText="Strinjam se">Spletno mesto uporablja pikotke, s katerimi izboljujemo uporabniko izkunjo.   <NavLink to="piskotki">Preberi ve</NavLink></CookieConsent>
        </>
    )
}