import React, { useEffect } from 'react';
import { Card, CardImg, CardImgOverlay, CardText, InputGroup, Input, Button } from 'reactstrap';
import { BASE_URL, COMPANY_API_TYPE } from '../../../helper/constants';
import { useTranslation } from "react-i18next";
import { ICompanyProps } from '../../../redux/interfaces';
import { useDispatch, useSelector } from 'react-redux';
import { fetchCompany } from '../../../redux/actions';
import { useNavigate } from 'react-router';
import { CommentModal } from '../CommentModal';
import { Helmet } from 'react-helmet';

export default function HomeBanner() {
  const [t] = useTranslation('common');
  const { useState } = React;
  const [searchClicked, setSearchClicked] = useState(false);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [isModalShow, setisModalShow] = useState(false);
  const [keywordError, setkeywordError] = useState<boolean | null>(null)

  const validateKeyword = (e: any) => {
    var keyword = e.target.value

    if (!keyword) {
      setkeywordError(false)
    } else {

      if (keyword.length < 3) {
        setkeywordError(true);
      } else {
        setkeywordError(false);
      }
    }
  }
  const companyStore = useSelector((state: any) => state.companyStore);
  const { error } = companyStore;

  const [formData, setFormData] = useState<ICompanyProps>({
    searchType: COMPANY_API_TYPE.SEARCH,
    keyword: ''
  });
  const handleChange = (e: any) => {
    const name = e.target.name;
    const value = e.target.value;
    setFormData({ ...formData, [name]: value });
  };
  const handleSearch = (e: any) => {
    const { keyword } = formData;

    if (!keyword || keywordError) {
      setkeywordError(true)
    } else {

      if (keyword.length >= 3) {

        setkeywordError(false)
        dispatch(fetchCompany(formData));
      } else {
        setkeywordError(true)

      }
    }
    setSearchClicked(true);
  }

  function handleScroll() {
    window.scroll({
      top: document.body.offsetHeight,
      left: 0,
      behavior: 'smooth',
    });
  }

  useEffect(() => {
    if (searchClicked) {
      if (keywordError===false) {
        navigate('/podjetja', { replace: true });
      } else {
        setkeywordError(true);
        setSearchClicked(false);
      }
    }
  }, [error, navigate,keywordError, searchClicked]);

  return (
    <>
      <Helmet>
        <meta charSet="utf-8" />
        <title>Preveri Podjetje - Anonimno oceni zaposlitev</title>
        <meta name="description" content="Preveri ali oceni delodajalce glede zaposlitve. Popolnoma anonimno in brez registracije !" />
        <meta name="keywords" content="preveri podjetje, zaposlitev,  ocena zaposlitve,  mnenje, ocena, anonimna ocena zaposlitve, preveri zaposlitev, delodajalec, zaposleni, delavec, delo, delovno mesto" />
      </Helmet>
      <div className="banner">
        <Card inverse>
          <CardImg
            alt="Card image cap"
            src={BASE_URL+'/meeting-conferences-package.jpg'}
          />
          <CardImgOverlay>
            <div className="container">
              <CardText tag="h1">
                {t('home_page.slider_title')}
                <InputGroup>
                  <Input
                    name="keyword"
                    value={formData.keyword}
                    placeholder={t('home_page.slider_input_placeholder')}
                    onChange={e => { handleChange(e); validateKeyword(e) }}
                    valid={keywordError === false}
                    invalid={keywordError !== null && keywordError === true}
                  />
                  <Button onClick={handleSearch}>
                    {t('home_page.slider_button_text')}
                  </Button>
                </InputGroup>
                <div className="text_description">
    <p>Za laje iskanje uporabite dolgi naziv podjetja</p>
       <p>Dolgi naziv najdete na  <a href="https://www.ajpes.si/" target="_blank">www.ajpes.si</a> </p>
    
</div>

              </CardText>
            </div>
          </CardImgOverlay>
        </Card>
        <Button className="addcomment" onClick={() => setisModalShow(!isModalShow)}>
          {t('home_page.add_a_comment')}
        </Button>
        <CommentModal isModalShow={isModalShow} setisModalShow={setisModalShow} source="HomeDetails" companyName="" />
        <button type="button" className="bottom_scroll" onClick={handleScroll}><img src={BASE_URL+'/arrow_down_optimized.png'} alt='arrow_down' /></button>

      </div>
    </>
  )
}