import { AnyAction } from 'redux';
import { INIT, LOADING, SUCCESS, ERROR } from '../../helper/constants';
import {
    SCOMPANY,
    SCOMPANY_SUCCESS,
    SCOMPANY_ERROR,
} from './constants';

interface ICompany {
    details: any;
    detailsPhase: string;
    message: string;
    searchType: string;
    keyword: string;
    region_id: string;
    currentPage: string;
    perPage: number;
    total: number;
    error: string;
}

const INIT_STATE: ICompany = {
    details: {},
    detailsPhase: INIT,
    message: '',
    searchType: '',
    keyword: '',
    region_id: '',
    currentPage: '',
    perPage: 0,
    total: 0,
    error: ''

}

const companyDetailsStore = (state = INIT_STATE, action: AnyAction) => {
    const { type, payload } = action;
    switch (type) {
            case SCOMPANY:
                return {
                    ...state,
                    detailsPhase: LOADING
                }
            case SCOMPANY_SUCCESS: 
                return {
                    ...state,
                    detailsPhase: SUCCESS,
                    error: null,
                    details: payload
                }
            case SCOMPANY_ERROR:
                return {
                    ...state,
                    detailsPhase: ERROR,
                    error: payload,
                    details: [],
                    message: payload
                }
        default:
            return {
                ...state
            }
    }
}

export {
    companyDetailsStore
}