import { AnyAction } from "redux";
import { call, put, takeLatest } from "redux-saga/effects"
import { newsDetailsAPI } from "./api";
import { SNEWS,SNEWS_SUCCESS, SNEWS_ERROR } from "./constants"

function* newsDetails(action: AnyAction): any {
    const { payload } = action;
    
    const response = yield call(newsDetailsAPI, payload);

    if (response.status) {
        yield put({ type: SNEWS_SUCCESS, payload: response.data });
    } else {
        yield put({ type: SNEWS_ERROR, payload: response.message });
    }
}

function* newsDetailsSaga() {
    yield takeLatest(SNEWS, newsDetails);
}

export {
    newsDetailsSaga,
}