import { AnyAction } from "redux";
import { call, put, takeLatest } from "redux-saga/effects"
import { companyDetailsAPI } from "./api";
import { SCOMPANY,SCOMPANY_SUCCESS, SCOMPANY_ERROR } from "./constants"

function* companyDetails(action: AnyAction): any {
    const { payload } = action;
    
    const response = yield call(companyDetailsAPI, payload);

    if (response.status) {
        yield put({ type: SCOMPANY_SUCCESS, payload: response.data });
    } else {
        yield put({ type: SCOMPANY_ERROR, payload: response.message });
    }
}

function* companyDetailsSaga() {
    yield takeLatest(SCOMPANY, companyDetails);
}

export {
    companyDetailsSaga,
}