import { AnyAction } from 'redux';
import { INIT, LOADING, SUCCESS, ERROR,contacted_success } from '../../helper/constants';
import {
    CONTACT,
    CONTACT_ERROR,
    CONTACT_RESET_ALERT,
    CONTACT_SUCCESS,
} from './constants';

interface IContact {
    phase: string;
    status: boolean;
    message: string
}

const INIT_STATE: IContact = {
    phase: INIT,
    status: false,
    message: ''

}

const contactStore = (state = INIT_STATE, action: AnyAction) => {
    const { type, payload } = action;
    switch (type) {
        case CONTACT:
            return {
                ...state,
                phase: LOADING
            }
        case CONTACT_RESET_ALERT:
                return {
                    ...state,
                    phase: INIT,
                    message: ''
                }
        case CONTACT_SUCCESS: 
            return {
                ...state,
                phase: SUCCESS,
                status: true,
                error: null,
                message: contacted_success
            }
        case CONTACT_ERROR:
            return {
                ...state,
                status: false,
                phase: ERROR,
                error: payload,
                message: payload
            }
        default:
            return {
                ...state
            }
    }
}

export {
    contactStore
}
