import axios from "axios";
import { API_URL, METHODS } from "../../helper/constants";
import { IContactProps } from "../interfaces";

const contactAPI: any = (data: IContactProps) => 
    axios({
        method: METHODS.POST,
        url: `${API_URL}/auth/csubmit-data`,
        headers: { 'Content-Type': 'application/json' },
        data
    });

export {
    contactAPI
}