import  {useEffect, useState} from 'react';
import { Row } from 'reactstrap';
import { Heading } from '../../../components/News/Heading';
import { Listing } from '../../../components/News/Listing';
import { INewsProps } from '../../../interfaces/INewsProps';
import { useDispatch, useSelector } from 'react-redux';
import { fetchNews } from '../../../redux/actions';
import { Helmet } from 'react-helmet';

export default function NewsList() {

    const [newsList, setNewsList] = useState<INewsProps|any>([]);

    const newsStore = useSelector((state: any) => state.newsStore)
    const { data } = newsStore;

    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(fetchNews());

    }, [dispatch]);

    useEffect(() => {
        setNewsList([...data]);
      }, [data, setNewsList]);

      
    return (
        <>
          <Helmet>
        <meta charSet="utf-8" />
        <title>Preveri Podjetje - novice</title>
        <meta name="description" content="novice"/>
      </Helmet>
        <Heading/>
        <div className="companyListing news_listing">
        <div className="container">
            <Row>
        {newsList.map((section: INewsProps, index: Number) => <Listing key={`Listing${index}`} {...section} />)}
        </Row>
            </div>
            </div>
            </>
    )
}