import { AnyAction } from 'redux';
import { INIT, LOADING, SUCCESS, ERROR,images_uploaded } from '../../helper/constants';
import {
    PICTURESUBMIT,
    PICTURESUBMIT_ERROR,
    PICTURESUBMIT_SUCCESS,
    PICTURESUBMIT_RESET_ALERT,
} from './constants';

interface IPictureSubmit {
    phase: string;
    status: boolean;
    message: string
}

const INIT_STATE: IPictureSubmit = {
    phase: INIT,
    status: false,
    message: ''

}

const pictureSubmitStore = (state = INIT_STATE, action: AnyAction) => {
    const { type, payload } = action;
    switch (type) {
        case PICTURESUBMIT:
            return {
                ...state,
                phase: LOADING
            }
        case PICTURESUBMIT_RESET_ALERT:
                return {
                    ...state,
                    phase: INIT,
                    message: ''
            }
        case PICTURESUBMIT_SUCCESS: 
            return {
                ...state,
                phase: SUCCESS,
                status: true,
                error: null,
                message: images_uploaded
            }
        case PICTURESUBMIT_ERROR:
            return {
                ...state,
                status: false,
                phase: ERROR,
                error: payload,
                message: payload
            }
        default:
            return {
                ...state
            }
    }
}

export {
    pictureSubmitStore
}
