import { AnyAction } from 'redux';
import { INIT, LOADING, SUCCESS, ERROR } from '../../helper/constants';
import {
    COMPANY,
    COMPANY_ERROR,
    COMPANY_SUCCESS,
    COMPANY_RESET_ALERT,
    REGION,
    REGION_SUCCESS,
    REGION_ERROR,
} from './constants';

interface ICompany {
    data: any;
    regions: any;
    details: any;
    phase: string;
    message: string;
    searchType: string;
    keyword: string;
    region_id: string;
    current_page: string;
    total: number;
    error: string;
    first_page_url: string;
    from: string;
    last_page: string;
    last_page_url: string;
    next_page_url: string;
    path: string;
    per_page: number;
    prev_page_url:string;
    to: string;

}

const INIT_STATE: ICompany = {
    data: [],
    regions: [],
    details : {},
    phase: INIT,
    message: '',
    searchType: '',
    keyword: '',
    region_id: '',
    current_page: '',
    first_page_url: '',
    from: '',
    last_page: '',
    last_page_url: '',
    next_page_url: '',
    path: '',
    per_page: 0,
    prev_page_url:'',
    to:'',
    total: 0,
    error: ''

}

const companyStore = (state = INIT_STATE, action: AnyAction) => {
    const { type, payload } = action;
    switch (type) {
        case COMPANY:
            const {
                searchType,
                keyword,
                region_id,
            } = payload;
            return {
                ...state,
                phase: LOADING,
                searchType,
                keyword,
                region_id,
            }
        case COMPANY_RESET_ALERT:
            return {
                ...state,
                phase: INIT,
                message: ''
            }
        case COMPANY_SUCCESS:
            return {
                ...state,
                phase: SUCCESS,
                error: null,
                data: payload.data,
                current_page : payload.current_page,
                first_page_url : payload.first_page_url,
                from : payload.from,
                last_page : payload.last_page,
                last_page_url : payload.last_page_url,
                next_page_url : payload.next_page_url,
                path : payload.path,
                per_page : payload.per_page,
                to : payload.to,
                total : payload.total,

            }
        case COMPANY_ERROR:
            return {
                ...state,
                phase: ERROR,
                error: payload,
                data: [],
                message: payload
            }
        case REGION:
            return {
                ...state,
                phase: LOADING
            }
        case REGION_SUCCESS:
            return {
                ...state,
                phase: SUCCESS,
                error: null,
                regions: payload
            }
        case REGION_ERROR:
            return {
                ...state,
                phase: ERROR,
                error: payload,
                regions: [],
                message: payload
            }
        default:
            return {
                ...state
            }
    }
}

export {
    companyStore
}