import { useEffect, useState } from 'react';
import { CardTitle, Row, Col } from 'reactstrap';
import 'react-multi-carousel/lib/styles.css';
import { IRatingProps } from '../../interfaces/IRatingProps';
import { LAST_COMMENT, RANDOM_SELECTED_COMPANY, WHAT_IS_SALARY, baseRating, SUCCESS, COMPANY_IMAGES_URL } from '../../helper/constants';
import { CompanySection, Rating, Section } from '../../components';
import { useTranslation } from "react-i18next";
import { HomeBanner } from '../../components/Home/HomeBanner';
import { useSelector } from 'react-redux';
import { ISectionProps } from '../../interfaces/ISectionProps';
import Carousel from 'react-multi-carousel';
import { ICompanySectionProps } from '../../interfaces/ICompanySectionProps';


export default function Home() {
  const [t] = useTranslation('common');
  const homeStore = useSelector((state: any) => state.homeStore);
  const { alldata,phase } = homeStore;

  const [randomCompany, setRandomCompany] = useState<IRatingProps>({
    id:1,
    type: RANDOM_SELECTED_COMPANY,
    companyImage: COMPANY_IMAGES_URL+'no_image.png',
    slug:'',
    description:'',
    companyName: '',
    comment: '',
    rating: 0,
    baseRating
  })

  const [lastComment, setLastComment] = useState<IRatingProps>({
    id:1,
    type: LAST_COMMENT,
    companyImage: COMPANY_IMAGES_URL+'no_image.png',
    slug:'',
    description:'',
    companyName: '',
    comment: '',
    rating: 0,
    baseRating
  })

  const [lastSalary, setLastSalary] = useState<IRatingProps>({
    id:1,
    type: WHAT_IS_SALARY,
    companyImage: COMPANY_IMAGES_URL+'no_image.png',
    slug:'',
    description:'',
    companyName: '',
    comment: '',
    rating: 0,
    baseRating
  })

  const [sections, setSections] = useState<any>([]);

const [companysections, setCompanySections] = useState<any>([]);

  const responsive = {
    desktop: {
        breakpoint: { max: 3000, min: 1024 },
        items: 3,
        slidesToSlide: 3 // optional, default to 1.
    },
    tablet: {
        breakpoint: { max: 1024, min: 464 },
        items: 2,
        slidesToSlide: 2 // optional, default to 1.
    },
    mobile: {
        breakpoint: { max: 464, min: 0 },
        items: 1,
        slidesToSlide: 1 // optional, default to 1.
    }
};


  useEffect(() => {

    setRandomCompany({
      id: alldata?.random_selected_company?.company?.id,
      type: RANDOM_SELECTED_COMPANY,
      companyImage: alldata?.random_selected_company?.company?.logo,
      companyName: alldata?.random_selected_company?.company?.title,
      description: alldata?.random_selected_company?.company?.description,
      slug: alldata?.random_selected_company?.company?.slug,
      comment: alldata?.random_selected_company?.good_stuff,
      rating: alldata?.random_selected_company?.rating,
      baseRating
    });

    setLastComment({
      id: alldata?.last_comment?.company?.id,
      type: LAST_COMMENT,
      companyImage: alldata?.last_comment?.company?.logo,
      companyName: alldata?.last_comment?.company?.title,
      description: alldata?.last_comment?.company?.description,
      slug: alldata?.last_comment?.company?.slug,
      comment: alldata?.last_comment?.management_advice,
      rating: alldata?.last_comment?.rating,
      baseRating
    });

    setLastSalary({
      id: alldata?.last_salary_comment?.company?.id,
      type: WHAT_IS_SALARY,
      companyImage: alldata?.last_salary_comment?.company?.logo,
      description: alldata?.last_salary_comment?.company?.description,
      companyName: alldata?.last_salary_comment?.company?.title,
      slug: alldata?.last_salary_comment?.company?.slug,
      comment: '',
      // comment: alldata?.last_salary_comment?.workplace,
      rating: alldata?.last_salary_comment?.rating,
      baseRating
    });

    const { home_page_sections,top_companies } = alldata;

    if(phase === SUCCESS)
    {
      setSections([...home_page_sections]);
      setCompanySections([...top_companies]);

    }

  }, [alldata,phase]);

  return (
    <>
      <HomeBanner />
      <div className="review">
        <div className="container">
          <Row>
            <Col sm="4">
              <Rating {...randomCompany} />
            </Col>
            <Col sm="4">
              <Rating {...lastComment} />
            </Col>
            <Col sm="4">
              <Rating {...lastSalary} />
            </Col>
          </Row>
        </div>
      </div>
      <div className="service">
        <div className="container">
        <Carousel
                additionalTransfrom={0}
                arrows={false}
                autoPlaySpeed={3000}
                centerMode={false}
                className=""
                containerClass="container-with-dots"
                dotListClass=""
                draggable
                focusOnSelect={false}
                infinite
                itemClass=""
                keyBoardControl
                minimumTouchDrag={80}
                renderButtonGroupOutside={false}
                renderDotsOutside={false}
                responsive={responsive}
                showDots={true}
                sliderClass=""
                slidesToSlide={1}
                swipeable
            >
            {sections?.map((section: ISectionProps, index: Number) => <Section key={`section${index}`} {...section} />
                 )}
            </Carousel>
        </div>
      </div>
      <div className="top_companies">
        <div className="container">
          <CardTitle tag="h3">
            {t('home_page.top_companies')}
          </CardTitle>
          {
            companysections.length=== 0 ? (
              <>
              <h2 style={{textAlign: "center"}}>{t('messages.no_top_company_found')}</h2>
              </>
            ) : 
          
          <Carousel
            additionalTransfrom={0}
            arrows={false}
            autoPlay={true}
            autoPlaySpeed={3000}
            centerMode={false}
            className=""
            containerClass="container-with-dots"
            dotListClass=""
            draggable
            focusOnSelect={false}
            infinite
            itemClass=""
            keyBoardControl
            minimumTouchDrag={80}
            renderButtonGroupOutside={false}
            renderDotsOutside={false}
            responsive={responsive}
            showDots={false}
            sliderClass=""
            slidesToSlide={1}
            swipeable
          >
            {companysections.map((section: ICompanySectionProps, index: Number) => <CompanySection key={`companySection${index}`} {...section} />
              )}
              </Carousel>
        }
        </div>
      </div>
    </>
  )

}