import React, { useEffect } from 'react';
import { Modal, ModalHeader, ModalBody, ModalFooter, Button, FormGroup, Label, Input, FormFeedback, Alert } from 'reactstrap';
import { useTranslation } from "react-i18next";
import StepWizard from "react-step-wizard";
import AsyncSelect from 'react-select/async';
import { companyNamesAPI } from '../../../redux/company/api';
import { ALERT_CLASS, ALERT_TIMEOUT, brightFuture, COMPANY_API_TYPE, employementType, environmentPositive, Experience, fullAmountSalary, generallySatisfied, imatePlace, lastTimeWorked, paidFairly, professionalTraining, properlyRegistered, rateCompany, rateSatisfaction, rateSatisfaction_new, regionLive, salaryRegular, spendHours, travelExpenses, weekendHours, yourJob } from '../../../helper/constants';
import { ICompanyCommentProps } from '../../../redux/interfaces';
import { Radio } from '../../../customComponents/Radio';
import { useDispatch } from 'react-redux';
import { commentSubmit } from '../../../redux/actions';
import { Link } from 'react-router-dom';
import { Checkbox } from '../../../customComponents/Checkbox';


export default function CommentModal(props: any) {

    const [t] = useTranslation('common');
    const { useState } = React;
    const dispatch = useDispatch();
    const { isModalShow, setisModalShow, source, companyName } = props;

    const [formData, setFormData] = useState<ICompanyCommentProps | any>({
        searchType: COMPANY_API_TYPE.FILTER,
        keyword: '',
        region_id: '',
        ip_address: '',
        position: '',
        amount: '',
        describe_company: '',
        good_stuff: '',
        bad_things: '',
        management_advice: '',
        regular_fairly: []
    });


    const [companyOption, setCompanyOption] = useState<any>();

    const [success, SetSuccess] = useState<any>();

    const [amount, setAmount] = useState<any>();

    const [lastError, SetLastError] = useState<any>();

    const [lastCheck, SetLastCheck] = useState<any>();

    const [error, setError] = useState<any>({});

    const companyNamesStyles = {
        container: () => ({
            width: 400,
            color: '#000',
            padding: 10,
        }),
        control: (provided: any, state: any) => {
            const borderColor = error && error.keyword ? 'red' : '#ffac06';
            return ({
                ...provided,
                borderRadius: 30,
                borderColor,
            });
        }
    };

    const loadOptions: any = async (input: string) => {
        let options: any[] = []
        await companyNamesAPI({
            title: input,
            region_id: ''
        }).then((response: any) => {
            options = response.data.map((item: any) => ({
                label: item.title,
                value: item.title
            }));
        }).catch((error: any) => {
        });
        return options;
    }

    const handleRadioChange = async (e: any, step: any, goToStep: any) => {
        const name = e.target.name;
        const value = e.target.value;
        await setFormData({ ...formData, [name]: value });
        await SetSuccess('');
        if (step === 31) {
            const data1 = { ...formData, [name]: value };
            if (!lastCheck) {
                SetLastError("Morate se strinjati z naimi pogoji ");
            } else {
                SetLastError('');
                submitForm(data1);
            }
        } else {
            goToStep(step);
        }
    };

    const handleCheckBoxChange = (e: any) => {
        const name = e.target.name;
        const value = e.target.value;
        let checkBoxData = formData[name] ? [...formData[name]] : [];
        if (!e.target.checked) {
            checkBoxData.push(value);
        } else {
            checkBoxData = checkBoxData.filter(item => item !== value);
        }
        setFormData({ ...formData, [name]: checkBoxData });
    }

    const handleLastChange = (e: any) => {

        if (e.target.checked) {
            SetLastCheck(true);
        } else {
            SetLastCheck(false);
        }
    }

    const submitForm = (data: any) => {
        if (success === '') {
            dispatch(commentSubmit(data));
            SetLastError('');
            SetSuccess('Hvala za va komentar, objavljen bo v kratkem.');
            setTimeout(() => {
                setisModalShow(false);
            }, ALERT_TIMEOUT);
        }
        // submit function definition
    }

    const validateCompany = (step: string, value: string | null = null) => {
        const error: any = {};

        switch (step) {
            case 'step1':
                const { keyword } = formData;

                if (keyword === '' || companyOption?.value === '') {
                    error['keyword'] = 'Napiite ime podjetja';
                }
                break;
            case 'step7':
                if (value?.length === 0) {
                    error['regular_fairly'] = t('company.select_at_least_one');
                }
                break;
            case 'step9':

                if (value === '') {
                    console.log('--check');
                    error['amount'] = 'Napiite ali izberite znesek oz. preskoite vpraanje';

                }
                break;
            case 'step10':
                if (value === '') {
                    error['position'] = 'Napiite delovno mesto';
                }
                break;
            case 'step16':
                if (value === '') {
                    error['describe_company'] = 'Prosimo, vnesite opis';
                }
                break;
            case 'step18':
                if (value === '') {
                    error['management_advice'] = 'Vnesite predloge za vodstvo';
                }
                break;
            case 'step25':
                if (value === '') {
                    error['good_stuff'] = 'Vpiite, kaj bi pohvalili';
                }
                break;
            case 'step28':
                if (value === '') {
                    error['bad_things'] = 'Prosimo, vpiite minuse v podjetju';
                }
                break;
            default:
                break;
        }
        setError(error);
        return error;
    }

    const First = (props: any) => {
        useEffect(() => {
            if (source === 'companyDetails' && props.currentStep === 1) {
                props.setCompanyOption({ label: companyName, value: companyName });
                props.setFormData({ ...formData, keyword: companyName });
                props.goToStep(2);
            }
        }, [props]);

        return (
            <>
                <FormGroup>
                    <p>
                        Katero podjetje elite oceniti ?
                    </p>
                    <AsyncSelect
                        defaultOptions={[{ value: '', label: 'Napiite ime podjetja' }]}
                        loadOptions={props.loadOptions}
                        onChange={(option: any) => {
                            props.setCompanyOption(option);
                            props.setFormData({ ...formData, keyword: option.label });
                            setError({ ...error, keyword: null });
                        }}
                        className={error && error.keyword && 'error'}
                        styles={props.companyNamesStyles}
                        value={props.companyOption}
                    />
                    {error && error.keyword && <Label className={`text-danger`}>{error.keyword}</Label>}
                    <Button type="button" onClick={() => {
                        const error = validateCompany('step1');
                        Object.keys(error).length === 0 && props.goToStep(2);
                    }}>
                        Potrdi
                    </Button>
      
                </FormGroup>
                
                <hr />
                <p> Oddajanje komentarja o zaposlitvi je popolnoma varno. Vse informacije so zaitene in anonimne. Z oddajo komentarja se strinjate s <Link to="/pogoji-uporabe">splonimi pogoji poslovanja</Link> in <Link to="/politika-zasebnosti">politiko zasebnosti</Link>.</p>
            </>
        )
    };

    const Second = (props: any) => <FormGroup >
        <p>
            1. Kdaj ste nazadnje delali v tem podjetju ?
        </p>
        {
            lastTimeWorked.map((value, index) =>
                <Radio
                    key={`inputLastTimeWorked${index}`}
                    name="last_time_worked"
                    value={value}
                    label={value}
                    onChange={(e: any) => handleRadioChange(e, 3, props.goToStep)}
                />
            )
        }
        <hr />
        <p>1/29</p>
        {
            source === 'companyDetails' ? <p> Oddajanje komentarja o zaposlitvi je popolnoma varno. Vse informacije so zaitene in anonimne. Z oddajo komentarja se strinjate s <Link to="/pogoji-uporabe">splonimi pogoji poslovanja</Link> in <Link to="/politika-zasebnosti">politiko zasebnosti</Link></p> : ''

        }

        {
            source === 'companyDetails' ? '' : <span className="back_btn" onClick={() => props.goToStep(1)}> Nazaj</span>
        }



    </FormGroup>


    const Third = (props: any) => <FormGroup >
        <p>
            2. Kako ocenjujete zaposlitev ?
        </p>
        {
            rateCompany.map((value, index) =>
                <Radio
                    key={`inputrateCompany${index}`}
                    name="company_rate"
                    value={value}
                    label={value}
                    onChange={(e: any) => handleRadioChange(e, 4, props.goToStep)}
                />
            )
        }
        <hr />
        <p>2/29</p>
        <span className="back_btn" onClick={() => props.goToStep(2)}> Nazaj </span>
    </FormGroup>
    const Fourth = (props: any) => <FormGroup >
        <p>
            3. Ali je plailo poteno ?
        </p>
        {
            paidFairly.map((value, index) =>
                <Radio
                    key={`inputpaidFairly${index}`}
                    name="paid_fairly"
                    value={value}
                    label={value}
                    onChange={(e: any) => handleRadioChange(e, 5, props.goToStep)}
                />
            )
        }
        <hr />
        <span className="back_btn" onClick={() => props.goToStep(3)}> Nazaj </span>
        <p>3/29</p>
    </FormGroup>

    const Fifth = (props: any) => <FormGroup >
        4. Ali dobivate redno plailo ?
        {
            salaryRegular.map((value, index) =>
                <Radio
                    key={`inputregularFairly${index}`}
                    name="regular_salary"
                    value={value}
                    label={value}
                    onChange={(e: any) => handleRadioChange(e, 6, props.goToStep)}
                />
            )
        }
        <hr />
        <span className="back_btn" onClick={() => props.goToStep(4)}> Nazaj </span>
        <p>4/29</p>
    </FormGroup>

    const Sixth = (props: any) => <FormGroup >
        5. Ali imate monost napredovanja pri plai ?
        {
            fullAmountSalary.map((value, index) =>
                <Radio
                    key={`inputfullamountSalary${index}`}
                    name="full_amount_salary"
                    value={value}
                    label={value}
                    onChange={(e: any) => handleRadioChange(e, 7, props.goToStep)}
                />
            )
        }
        <hr />
        <span className="back_btn" onClick={() => props.goToStep(5)}> Nazaj </span>
        <p>5/29</p>
    </FormGroup>


    const Seventh = (props: any) => <FormGroup >
        6. Katere monosti zaposlitve podjetje ponuja ?  (izberete lahko ve odgovorov)
        {
            properlyRegistered.map((value, index) =>
                <Checkbox
                    key={`inputregularFairly${index}`}
                    name="regular_fairly"
                    value={value}
                    label={value}
                    checked={formData.regular_fairly && formData.regular_fairly.some((item: any) => item === value)}
                    onChange={(e: any) => handleCheckBoxChange(e)}
                />
            )
        }
        <FormFeedback invalid={!!error.regular_fairly}>
            {error?.regular_fairly}
        </FormFeedback>
        <hr />
        <Button onClick={() => {
            const error = validateCompany('step7', formData.regular_fairly);
            if (Object.keys(error).length === 0) {
                props.goToStep(8);
            }
        }}>
            Potrdi
        </Button>
        <span className="back_btn" onClick={() => props.goToStep(6)}> Nazaj </span>
        <p>6/29</p>
    </FormGroup>


    const Eighth = (props: any) => <FormGroup >
        7. Katero obliko zaposlitve imate?
        {
            employementType.map((value, index) =>
                <Radio
                    key={`inputemployementType${index}`}
                    name="employement_type"
                    value={value}
                    label={value}
                    onChange={(e: any) => handleRadioChange(e, 9, props.goToStep)}
                />
            )
        }
        <hr />
        <span className="back_btn" onClick={() => props.goToStep(7)}> Nazaj </span>
        <p>7/29</p>
    </FormGroup>

    const Ninth = (props: any) => {
        const rx_live = /^[+-]?\d*(?:[.,]\d*)?$/;

        const [amount, setAmount] = useState(rx_live.test(formData['amount']) ? formData['amount'] : '');
        return (<FormGroup >
            8. Viina mesene neto plae v EUR (izberi/napii doloen znesek)
            <br />
            <Input
                name="amount"
                type="text"
                value={amount}
                invalid={!!error?.amount ||  !rx_live.test(amount)}
                onChange={(e) => setAmount(e.target.value)}
                pattern="^-?[0-9]\d*\.?\d*$"
            />
            <FormFeedback invalid={!!error?.amount}>
                {error?.amount}
            </FormFeedback>
            {
                ['500', '800', '1.000', '1.200', '1.500'].map((amountVal: string, index: Number) => (<>
                    <Input
                        name="select_amount"
                        type="radio"
                        value={amountVal}
                    />
                    {' '}
                    <Label check onClick={() => setAmount(amountVal)}>
                        {amountVal}
                    </Label>
                </>))
            }


            <Button onClick={() => {
                const rx_live = /^[+-]?\d*(?:[.,]\d*)?$/;

                const error = validateCompany('step9', amount);
                if (Object.keys(error).length === 0 && rx_live.test(amount)===true) 
                {
                    setFormData({ ...formData, amount })
                    props.goToStep(10);
                } else {
                    setAmount('');
                }
            }}>
                Potrdi
            </Button>
            <span className="skip_btn" onClick={() => props.goToStep(10)}>Preskoi</span>

            <hr />
            <span className="back_btn" onClick={() => props.goToStep(8)}> Nazaj </span>
            <p>8/29</p>
        </FormGroup>);
    }

    const Tenth = (props: any) => {
        const [position, setPosition] = useState(formData['position'])
        return (<FormGroup >
            9. Na katerem delovnem mestu ste zaposleni ?
            <br />
            <Input
                name="position"
                type="text"
                value={position}
                invalid={!!error?.position}
                onChange={(e) => setPosition(e.target.value)}
                placeholder="Primer: Administrator - Administracija"
            />
            <FormFeedback invalid={!!error.position}>
                {error?.position}
            </FormFeedback>
            <Button onClick={() => {
                const error = validateCompany('step10', position);
                if (Object.keys(error).length === 0) {
                    setFormData({ ...formData, position })
                    props.goToStep(11);
                }
            }}>
                Potrdi
            </Button>
            <span className="skip_btn" onClick={() => props.goToStep(11)}>Preskoi</span>

            <hr />
            <span className="back_btn" onClick={() => props.goToStep(9)}> Nazaj </span>
            <p>9/29</p>
        </FormGroup>);
    };

    const Eleventh = (props: any) => <FormGroup >
        10. V kateri regiji ste zaposleni ?
        {
            regionLive.map((value, index) =>
                <Radio
                    key={`inputregionlive${index}`}
                    name="region_live"
                    value={value}
                    label={value}
                    onChange={(e: any) => handleRadioChange(e, 12, props.goToStep)}
                />
            )
        }
        <hr />
        <span className="back_btn" onClick={() => props.goToStep(10)}> Nazaj </span>
        <p>10/29</p>
    </FormGroup>

    const Twelth = (props: any) => <FormGroup >
        11. Koliko ur na dan ste povreno v slubi ?
        {
            spendHours.map((value, index) =>
                <Radio
                    key={`inputspendHours${index}`}
                    name="spend_hours"
                    value={value}
                    label={value}
                    onChange={(e: any) => handleRadioChange(e, 13, props.goToStep)}
                />
            )
        }
        <hr />
        <span className="back_btn" onClick={() => props.goToStep(11)}> Nazaj </span>
        <p>11/29</p>
    </FormGroup>

    const Thirteen = (props: any) => <FormGroup >
        12. Ali delate za vikende ? (sobota ali nedelja)
        {
            weekendHours.map((value, index) =>
                <Radio
                    key={`inputweekendHours${index}`}
                    name="weekend_works"
                    value={value}
                    label={value}
                    onChange={(e: any) => handleRadioChange(e, 14, props.goToStep)}
                />
            )
        }

        <hr />

        <span className="back_btn" onClick={() => props.goToStep(12)}> Nazaj </span>
        <p>12/29</p>
    </FormGroup>

    const Fourtenth = (props: any) => <FormGroup >
        13. Kako ste zadovoljni s svojo plao ?  (ocena 1-10)

        {
            rateSatisfaction.map((value, index) =>
                <Radio
                    key={`inputannualSatisf${index}`}
                    name="annual_satisfaction"
                    value={value}
                    label={value}
                    onChange={(e: any) => handleRadioChange(e, 15, props.goToStep)}
                />
            )
        }
        <hr />
        <span className="back_btn" onClick={() => props.goToStep(13)}> Nazaj </span>
        <p>13/29</p>
    </FormGroup>

    const Fifteenth = (props: any) => <FormGroup >
        14. Kako ste zadovoljni z delom direktorja ?  (ocena 1-10)

        {
            rateSatisfaction_new.map((value, index) =>
                <Radio
                    key={`inputdirectorRate${index}`}
                    name="director_rate"
                    value={value}
                    label={value}
                    onChange={(e: any) => handleRadioChange(e, 16, props.goToStep)}
                />
            )
        }
        <hr />
        <span className="back_btn" onClick={() => props.goToStep(14)}> Nazaj </span>
        <p>14/29</p>
    </FormGroup>


    const Sixtenth = (props: any) => {
        const [describeCompany, setDescribeCompany] = useState(formData['describe_company']);
        return (<FormGroup >
            15. Na kratko opiite podjetje
            <br />
            <Input
                name="describe_company"
                type="text"
                value={describeCompany}
                invalid={!!error?.describe_company}
                onChange={(e) => setDescribeCompany(e.target.value)}
            />
            <p>Z nekaj besedami opiite podjetje oziroma zaposlitev.</p>
            <FormFeedback invalid={!!error?.describe_company}>
                {error?.describe_company}
            </FormFeedback>
            <Button onClick={() => {
                const error = validateCompany('step16', describeCompany);
                if (Object.keys(error).length === 0) {
                    setFormData({ ...formData, describe_company: describeCompany })
                    props.goToStep(17);
                }
            }}>
                Potrdi
            </Button>
            <span className="skip_btn" onClick={() => props.goToStep(17)}>Preskoi</span>

            <hr />
            <span className="back_btn" onClick={() => props.goToStep(15)}> Nazaj </span>
            <p>15/29</p>
        </FormGroup>);
    };

    const Sevententh = (props: any) => <FormGroup >
        16. Kako ste zadovoljni z  vodenjem podjetja ?  (ocena 1-10)

        {
            rateSatisfaction.map((value, index) =>
                <Radio
                    key={`inputmanagementRate${index}`}
                    name="management_rate"
                    value={value}
                    label={value}
                    onChange={(e: any) => handleRadioChange(e, 18, props.goToStep)}
                />
            )
        }
        <hr />
        <span className="back_btn" onClick={() => props.goToStep(16)}> Nazaj </span>
        <p>16/29</p>
    </FormGroup>

    const Eigthenth = (props: any) => {
        const [managementAdvice, setManagementAdvice] = useState(formData['management_advice']);
        return (<FormGroup >
            17. Kaj bi predlagali vodstvu ?
            <br />
            <Input
                name="management_advice"
                type="text"
                value={managementAdvice}
                invalid={!!error?.management_advice}
                onChange={(e) => setManagementAdvice(e.target.value)}
            />
            <p>Napiite predloge, kaj bi po vae lahko odgovorne osebe izboljale.</p>
            <FormFeedback invalid={!!error?.management_advice}>
                {error?.management_advice}
            </FormFeedback>
            <Button onClick={() => {
                const error = validateCompany('step18', managementAdvice);
                if (Object.keys(error).length === 0) {
                    setFormData({ ...formData, management_advice: managementAdvice })
                    props.goToStep(19);
                }
            }}>
                Potrdi
            </Button>
            <span className="skip_btn" onClick={() => props.goToStep(19)}>Preskoi</span>
            <hr />
            <span className="back_btn" onClick={() => props.goToStep(17)}> Nazaj </span>
            <p>17/29</p>
        </FormGroup>);
    };


    const Ninetenth = (props: any) => <FormGroup >
        18. Kako ste zadovoljni z lastnikom podjetja ?  (ocena 1-10)

        {
            rateSatisfaction_new.map((value, index) =>
                <Radio
                    key={`inputceoRate${index}`}
                    name="ceo_rate"
                    value={value}
                    label={value}
                    onChange={(e: any) => handleRadioChange(e, 20, props.goToStep)}
                />
            )
        }
        <hr />
        <span className="back_btn" onClick={() => props.goToStep(18)}> Nazaj </span>
        <p>18/29</p>
    </FormGroup>

    const Twenty = (props: any) => <FormGroup >
        19. Kako ste zadovoljni s sodelavci ?  (ocena 1-10)

        {
            rateSatisfaction.map((value, index) =>
                <Radio
                    key={`inputcolleagueRate${index}`}
                    name="colleague_rate"
                    value={value}
                    label={value}
                    onChange={(e: any) => handleRadioChange(e, 21, props.goToStep)}
                />
            )
        }

        <hr />
        <p>19/29</p>
        <span className="back_btn" onClick={() => props.goToStep(19)}> Nazaj </span>
    </FormGroup>


    const TwentyOne = (props: any) => <FormGroup >
        20. Koliko delovnih izkuenj imate ?

        {
            Experience.map((value, index) =>
                <Radio
                    key={`inputexperience${index}`}
                    name="experience"
                    value={value}
                    label={value}
                    onChange={(e: any) => handleRadioChange(e, 22, props.goToStep)}
                />
            )
        }
        <hr />
        <span className="back_btn" onClick={() => props.goToStep(20)}> Nazaj </span>
        <p>20/29</p>
    </FormGroup>

    const TwentyTwo = (props: any) => <FormGroup >
        21. Ali ima podjetje svetlo prihodnost ?

        {
            brightFuture.map((value, index) =>
                <Radio
                    key={`inputbrightFuture${index}`}
                    name="bright_future"
                    value={value}
                    label={value}
                    onChange={(e: any) => handleRadioChange(e, 23, props.goToStep)}
                />
            )
        }
        <hr />
        <span className="back_btn" onClick={() => props.goToStep(21)}> Nazaj </span>
        <p>21/29</p>
    </FormGroup>

    const TwentyThree = (props: any) => <FormGroup >
        22. Kakno je vae delo ?

        {
            yourJob.map((value, index) =>
                <Radio
                    key={`inputyourJob${index}`}
                    name="your_job"
                    value={value}
                    label={value}
                    onChange={(e: any) => handleRadioChange(e, 24, props.goToStep)}
                />
            )
        }
        <hr />
        <span className="back_btn" onClick={() => props.goToStep(22)}> Nazaj </span>
        <p>22/29</p>
    </FormGroup>

    const TwentyFourth = (props: any) => <FormGroup >
        23. Ali je vae delovno okolje pozitivno ?

        {
            environmentPositive.map((value, index) =>
                <Radio
                    key={`inputenvironmentPositive${index}`}
                    name="environment_positive"
                    value={value}
                    label={value}
                    onChange={(e: any) => handleRadioChange(e, 25, props.goToStep)}
                />
            )
        }
        <hr />
        <span className="back_btn" onClick={() => props.goToStep(23)}> Nazaj </span>
        <p>23/29</p>
    </FormGroup>

    const TwentyFifth = (props: any) => {
        const [goodStuff, setGoodStuff] = useState(formData['good_stuff']);
        return (<FormGroup >
            24. Kaj bi pohvalili ?
            <br />
            <Input
                name="good_stuff"
                type="text"
                value={goodStuff}
                invalid={!!error?.good_stuff}
                onChange={(e) => setGoodStuff(e.target.value)}
            />
            <FormFeedback invalid={!!error?.good_stuff}>
                {error?.good_stuff}
            </FormFeedback>
            <Button onClick={() => {
                const error = validateCompany('step25', goodStuff);
                if (Object.keys(error).length === 0) {
                    setFormData({ ...formData, good_stuff: goodStuff })
                    props.goToStep(26);
                }
            }}>
                Potrdi
            </Button>
            <span className="skip_btn" onClick={() => props.goToStep(26)}>Preskoi</span>
            <hr />
            <span className="back_btn" onClick={() => props.goToStep(24)}> Nazaj </span>
            <p>24/29</p>
        </FormGroup>);
    };


    const TwentySixth = (props: any) => <FormGroup >
        25. Ali radi prihajate na delo  ?

        {
            generallySatisfied.map((value, index) =>
                <Radio
                    key={`inputgenerallySatisfied${index}`}
                    name="generally_satisfied"
                    value={value}
                    label={value}
                    onChange={(e: any) => handleRadioChange(e, 27, props.goToStep)}
                />
            )
        }
        <hr />
        <span className="back_btn" onClick={() => props.goToStep(25)}> Nazaj </span>
        <p>25/29</p>
    </FormGroup>

    const TwentySeventh = (props: any) => <FormGroup >
        26. Ali podjetje plauje potne stroke ?

        {
            travelExpenses.map((value, index) =>
                <Radio
                    key={`inputtravelExpense${index}`}
                    name="travel_expenses"
                    value={value}
                    label={value}
                    onChange={(e: any) => handleRadioChange(e, 28, props.goToStep)}
                />
            )
        }
        <hr />
        <span className="back_btn" onClick={() => props.goToStep(26)}> Nazaj </span>
        <p>26/29</p>
    </FormGroup>

    const TwentyEigth = (props: any) => {
        const [badThings, setBadThings] = useState(formData['bad_things']);
        return (<FormGroup >
            27. S im niste zadovoljni v podjetju ?
            <br />
            <Input
                name="bad_things"
                type="text"
                value={badThings}
                invalid={!!error.bad_things}
                onChange={(e) => setBadThings(e.target.value)}
            />
            <p>Napiite, kateri so minusi podjetja</p>
            <FormFeedback invalid={!!error.bad_things}>
                {error?.bad_things}
            </FormFeedback>
            <Button onClick={() => {
                const error = validateCompany('step28', badThings);
                if (Object.keys(error).length === 0) {
                    setFormData({ ...formData, bad_things: badThings })
                    props.goToStep(29);
                }
            }}>
                Potrdi
            </Button>
            <span className="skip_btn" onClick={() => props.goToStep(29)}>Preskoi</span>
            <hr />
            <span className="back_btn" onClick={() => props.goToStep(27)}> Nazaj </span>
            <p>27/29</p>
        </FormGroup>);
    };

    const TwentyNinth = (props: any) => <FormGroup >
        {
            <>
                28. Ali vam podjetje plauje izobraevanja ? (uenje tujih jezikov, pridobitev certifikatov )
                {
                    professionalTraining.map((value, index) =>
                        <Radio
                            key={`inputprofessionalTraining${index}`}
                            name="paid_prof"
                            value={value}
                            label={value}
                            onChange={(e: any) => handleRadioChange(e, 30, props.goToStep)}
                        />
                    )
                }
                <hr />
                <span className="back_btn" onClick={() => props.goToStep(28)}> Nazaj </span>
                <p>28/29</p>
            </>
        }

    </FormGroup>

    const Thirty = (props: any) => <FormGroup >

        <Alert color={ALERT_CLASS.SUCCESS} isOpen={!!success}>
            {success}
        </Alert>
        {!success &&
            <>
                29. Ali imate plaane nadure ?
                {
                    <p style={{ color: "red" }}>{lastError}</p>
                }
                {
                    imatePlace.map((value, index) =>
                        <Radio
                            key={`inputimatePlace${index}`}
                            name="imate_place"
                            value={value}
                            label={value}
                            onChange={(e: any) => handleRadioChange(e, 31, props.goToStep(30))}
                            disabled={!!success}
                        />
                    )
                }
                <hr />
                <div className="last_check">
                    <input type="checkbox" name="verify" checked={lastCheck} onChange={(e: any) => handleLastChange(e)} style={{ marginRight:"6px;"}} />
                    <label>Pred oddajo komentarja je uporabnik prebral Splone pogoje poslovanja in Politiko zasebnosti Portala in se z obema izrecno strinja. </label>
                </div>
                <span className="back_btn" onClick={() => props.goToStep(29)}> Nazaj </span>
                <p>29/29</p>
            </>
        }
    </FormGroup>

    const firstProps = {
        loadOptions,
        companyOption,
        companyNamesStyles,
        setFormData,
        setCompanyOption
    }
    return (
        <>

            <Modal backdrop={false} fade={false} isOpen={isModalShow}>
                <ModalHeader
                    close={<button className="close" onClick={() => setisModalShow(!isModalShow)}></button>}
                    toggle={() => setisModalShow(!isModalShow)}
                >
                    Vpraalnik
                </ModalHeader>
                <ModalBody>
                    <StepWizard >
                        <First {...firstProps} />
                        <Second />
                        <Third />
                        <Fourth />
                        <Fifth />
                        <Sixth />
                        <Seventh />
                        <Eighth />
                        <Ninth />
                        <Tenth />
                        <Eleventh />
                        <Twelth />
                        <Thirteen />
                        <Fourtenth />
                        <Fifteenth />
                        <Sixtenth />
                        <Sevententh />
                        <Eigthenth />
                        <Ninetenth />
                        <Twenty />
                        <TwentyOne />
                        <TwentyTwo />
                        <TwentyThree />
                        <TwentyFourth />
                        <TwentyFifth />
                        <TwentySixth />
                        <TwentySeventh />
                        <TwentyEigth />
                        <TwentyNinth />
                        <Thirty submitForm={submitForm} />
                    </StepWizard>

                </ModalBody>
                <ModalFooter>

                </ModalFooter>
            </Modal>
        </>
    )
}