import  { useEffect, useState } from 'react';
import { NavLink } from 'react-router-dom';
import { Collapse, Nav, Navbar, NavbarBrand, NavItem, NavbarToggler } from 'reactstrap';
import { useTranslation } from "react-i18next";
import { BASE_URL } from '../../helper/constants';

export default function NavBar() {

    const [isCollapsed, setIsCollapsed] = useState(false);
    const [t] = useTranslation('common');
      useEffect(() => {
        window.scrollTo(0, 0);
    }, []);
    return (
        <>
            <Navbar container={true}
                color="light"
                expand="md"
                light
            >
                <NavbarBrand tag="div">
                    <NavLink to="/" reloadDocument>
                    
                        <img src={BASE_URL+'/logo.png'} alt='preveri' width='100' />
                    </NavLink>
                </NavbarBrand>
                <NavbarToggler onClick={() => setIsCollapsed(!isCollapsed)} />
                <Collapse navbar isOpen={isCollapsed}>
                    <Nav
                        className="me-2"
                        navbar
                        onClick={() => setIsCollapsed(!isCollapsed)}
                    >

                        <NavItem>
                            <NavLink to="podjetja" >
                                {t('navbar.what_are_the_company_like')}
                            </NavLink>
                        </NavItem>
                       
                        <NavItem>
                            <NavLink to="novice">
                                {t('navbar.news')}
                            </NavLink>
                        </NavItem>
                       
                    </Nav>
                </Collapse>
            </Navbar>
        </>);
}