import { AnyAction } from "redux";
import { call, put, takeLatest } from "redux-saga/effects"
import { pictureSubmitAPI } from "./api";
import { PICTURESUBMIT, PICTURESUBMIT_ERROR, PICTURESUBMIT_SUCCESS } from "./constants"

function* pictureSubmit(action: AnyAction): any {
    const { payload } = action;

    const response = yield call(pictureSubmitAPI, payload);

    if (response.status) {
        yield put({ type: PICTURESUBMIT_SUCCESS, payload: response.data });
    } else {
        yield put({ type: PICTURESUBMIT_ERROR, payload: response.message });
    }
}


function* pictureSubmitSaga() {
    yield takeLatest(PICTURESUBMIT, pictureSubmit);
}

export {
    pictureSubmitSaga
}