import { AnyAction } from "redux";
import { call, put, takeLatest } from "redux-saga/effects"
import { contactAPI } from "./api";
import { CONTACT, CONTACT_ERROR, CONTACT_SUCCESS } from "./constants"

function* contact(action: AnyAction): any {
    const { payload } = action;
    const response = yield call(contactAPI, payload);
    if (response.status) {
        yield put({ type: CONTACT_SUCCESS, payload: response.message });     
    } else {
        yield put({ type: CONTACT_ERROR, payload: response.message });     
    }
}

function* contactSaga() {
    yield takeLatest(CONTACT, contact);
}

export {
    contactSaga
}