import axios from "axios";
import { API_URL, METHODS } from "../../helper/constants";

const companyAPI: any = (params: any) =>
    axios({
        method: METHODS.GET,
        url: `${API_URL}/auth/company-list`,
        headers: { 'Content-Type': 'application/json' },
        params
    });
const companyNamesAPI: any = (params: any) =>
    axios({
        method: METHODS.GET,
        url: `${API_URL}/auth/company-names`,
        headers: { 'Content-Type': 'application/json' },
        params
    });
const regionAPI: any = () =>
    axios({
        method: METHODS.GET,
        url: `${API_URL}/auth/region-list`,
        headers: { 'Content-Type': 'application/json' },
    });

export {
    companyAPI,
    companyNamesAPI,
    regionAPI,
}