import React, { useEffect } from "react";
import { Card, Button, InputGroup } from 'reactstrap';
import { useTranslation } from "react-i18next";
import { ICompanyProps } from "../../../redux/interfaces";
import { COMPANY_API_TYPE, STORES } from "../../../helper/constants";
import { fetchCompany } from "../../../redux/actions";
import { useDispatch } from "react-redux";
import AsyncSelect from 'react-select/async';
import { AlertMessage } from "../../AlertMessage";
import { companyNamesAPI } from "../../../redux/company/api";

export default function Filter(props: any) {
    const { useState } = React;
    const [formData, setFormData] = useState<ICompanyProps>({
        searchType: COMPANY_API_TYPE.FILTER,
        keyword: '',
        region_id: ''
    });
    const [companyOption, setCompanyOption] = useState<any>();
    const [t] = useTranslation('common');
    const { regions, searchType, keyword, region_id } = props;
    const dispatch = useDispatch();
    useEffect(() => {
        if (searchType === COMPANY_API_TYPE.FILTER && formData.region_id !== region_id && formData.keyword !== keyword) {
            setCompanyOption({ label: keyword, value: keyword });
            setFormData({ ...formData, region_id, keyword, searchType });
        }
    }, [searchType, keyword, region_id, regions, formData]);

    const companyNamesStyles = {
        container: () => ({
            width: 400,
            color: '#000',
            padding: 10,
            borderColor: '#ffac06',
            zIndex:'3'
        })
    }

    const loadOptions: any = async (input: string) => {
        let options: any[] = []
        await companyNamesAPI({
            title: input,
            region_id: formData.region_id
        }).then((response: any) => {
            options = response.data.map((item: any) => ({
                label: item.title,
                value: item.title
            }));
        }).catch((error: any) => {
        });
        return options;
    }


    const handleSearch = (e: any) => {
        const { keyword, region_id } = formData;

        if (!keyword && !region_id) {

        } else if(keyword=== 'Ii po imenu podjetja') {
            
        } else {
            dispatch(fetchCompany(formData));
        }
    }

    return (
        <>
            <div className="company_banner">
                <Card inverse>
                    <div className="container">
                        <div className="SearchBar">
                            <InputGroup>
                                <AsyncSelect
                                    defaultOptions={[{ value: '', label: t('company.select_company_name') }]}
                                    loadOptions={loadOptions}
                                    onChange={(option: any) => {
                                        setCompanyOption(option);
                                        setFormData({ ...formData, keyword: option.label });
                                    }}
                                    styles={companyNamesStyles}
                                    value={companyOption}
                                />
                                <Button onClick={handleSearch}>
                                    {t('home_page.slider_button_text')}
                                </Button>
                            </InputGroup>
                        </div>
                    </div>
                </Card>
            </div>
            <AlertMessage store={STORES.COMPANY_STORE} />

        </>
    )
}