import axios from "axios";
import { API_URL, METHODS } from "../../helper/constants";

const newsAPI: any = (params: any) =>
    axios({
        method: METHODS.GET,
        url: `${API_URL}/auth/news-list`,
        headers: { 'Content-Type': 'application/json' },
        params
    });

export {
    newsAPI,
}