import { AnyAction } from "redux";
import { call, put, takeLatest } from "redux-saga/effects"
import { newsAPI } from "./api";
import { NEWS, NEWS_ERROR, NEWS_SUCCESS } from "./constants"

function* newsList(action: AnyAction): any {
    let params = {};
    const response = yield call(newsAPI, params);

    if (response.status) {
        yield put({ type: NEWS_SUCCESS, payload: response.data.data });
    } else {
        yield put({ type: NEWS_ERROR, payload: response.message });
    }
}

function* newsSaga() {
    yield takeLatest(NEWS, newsList);
}

export {
    newsSaga
}