import React from "react";
import { ISectionProps } from "../../../interfaces/ISectionProps";
import { Card, CardImg, CardText, CardTitle } from 'reactstrap';
import { useTranslation } from "react-i18next";
import { HOME_IMAGES_URL } from "../../../helper/constants";

export default function Section(props:ISectionProps) {
    const [t] = useTranslation('common');

    const { useState } = React;
    const [readMore, setReadMore] = useState(false);

    const {
       title,
       image,
       short_description
      } = props;
    return (
        <>
                    <Card body>
                        <div className="carusal_image">
                            <CardImg
                                alt={title}
                                src={HOME_IMAGES_URL+image}
                                top
                                width="20%"
                            />
                        </div>
                        <div className="carusal_text">
                            <CardTitle tag="h3">
                                {title}
                            </CardTitle>
                            <CardText>

                            <div dangerouslySetInnerHTML={{__html: readMore ? short_description : short_description.substring(0, 49)} } />

                               
                            </CardText>
                            <span style={{ cursor: 'pointer' }} onClick={() => setReadMore(!readMore)}>{readMore ? t('home_page.hide') : t('home_page.read_more')}</span>
                        </div>
                    </Card>
        </>
    )
}