import axios from "axios";
import { API_URL, METHODS } from "../../helper/constants";

const companyDetailsAPI: any = (params: any) =>
    axios({
        method: METHODS.GET,
        url: `${API_URL}/auth/company-details`,
        headers: { 'Content-Type': 'application/json' },
        params
    });

export {
    companyDetailsAPI
}