import { call, put, takeLatest } from "redux-saga/effects"
import { homeAPI } from "./api";
import { HOME, HOME_ERROR, HOME_SUCCESS } from "./constants"

function* homeList(): any {
    const response = yield call(homeAPI);

    if (response.status) {
        yield put({ type: HOME_SUCCESS, payload: response.data });     
    } else {
        yield put({ type: HOME_ERROR, payload: response.message });     
    }
}

function* homeSaga() {
    yield takeLatest(HOME, homeList);
}

export {
    homeSaga
}