import { Row, Col } from 'reactstrap';
import { Link,useParams } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { FacebookShareButton } from "react-share"
import { NEWS_URL } from "../../../helper/constants";
import Moment from 'moment';

export default function SingleNews(props:any) {
    const [t] = useTranslation('common');
    const {title,description,date} = props;
    const { id } = useParams();

return (
    <>
        <div className="single_news">
            <div className="container">
                <Row>
                    <Col sm="12">
                        <div className="single_news_details">
                            <h1>{title}</h1>
                            <p>{Moment(date).format('MMM DD, YYYY')}</p>
                            <br/>
                             <FacebookShareButton
                                url={NEWS_URL+id}
                                quote={title}
                                hashtag={title}>
                                <img
                                alt="Card_image_cap"
                                src="/fb.png"
                                width="40px"
                            />{t('company.share')}
                            </FacebookShareButton>
                                  <div dangerouslySetInnerHTML={ {__html: description} } />
                        </div>
                    </Col>
                </Row>
                <Link to="/novice" className="back_login">{t('news.back_to_news')}</Link>
            </div>
        </div>
    </>
)
}