import { AnyAction } from 'redux';
import { INIT, LOADING, SUCCESS, ERROR } from '../../helper/constants';
import {
    SNEWS,
    SNEWS_SUCCESS,
    SNEWS_ERROR,
} from './constants';

interface INews {
    details: any;
    detailsPhase: string;
    message: string;
    currentPage: string;
    perPage: number;
    total: number;
    error: string;
}

const INIT_STATE: INews = {
    details: {},
    detailsPhase: INIT,
    message: '',
    currentPage: '',
    perPage: 0,
    total: 0,
    error: ''

}

const newsDetailsStore = (state = INIT_STATE, action: AnyAction) => {
    const { type, payload } = action;
    switch (type) {
            case SNEWS:
                return {
                    ...state,
                    detailsPhase: LOADING
                }
            case SNEWS_SUCCESS: 
                return {
                    ...state,
                    detailsPhase: SUCCESS,
                    error: null,
                    details: payload
                }
            case SNEWS_ERROR:
                return {
                    ...state,
                    detailsPhase: ERROR,
                    error: payload,
                    details: [],
                    message: payload
                }
        default:
            return {
                ...state
            }
    }
}

export {
    newsDetailsStore
}