import React from 'react';
import 'react-multi-carousel/lib/styles.css';
import { Link } from "react-router-dom";
import { Card, Row, Col, CardTitle } from 'reactstrap';
import { useTranslation } from "react-i18next";
import { useDispatch } from 'react-redux';
import { useParams } from 'react-router';
import { Banner } from '../../../components/Company/Banner';
import { CommentModal } from '../../../components/Home/CommentModal';
import { Details } from '../../../components/Company/Details';
import { Comments } from '../../../components/Company/Comments';
import { BasicInfo } from '../../../components/Company/BasicInfo';
import { fetchCompanyDetails } from '../../../redux/actions';
import { connect } from 'react-redux';
import { useEffect, useState } from 'react';
import { Helmet } from 'react-helmet';
import { useNavigate } from 'react-router';
import Alert from 'react-popup-alert'
import { BASE_URL } from '../../../helper/constants';

const CompanyDetails = (props: any) => {
  const [t] = useTranslation('common');
  const { id } = useParams();
  const navigate = useNavigate();

  const [company, setCompany] = useState({
    city: '',
    total_employees: 0,
    reg_no: 0,
    legal_organizational_form: '',
    street: '',
    house_no: '',
    postal_code: '',
    post_office: 0,
    description: '',
    description_status: 0,
    address: '',
    region: null,
    comments: null,
    pictures: null,
    salaries: null,
    status:1,
  });

  const [alert, setAlert] = React.useState({
    type: 'error',
    text: 'This is a alert message',
    show: false
  })

  const dispatch = useDispatch();


  useEffect(() => {
    const ID:any = id;

    dispatch(fetchCompanyDetails({ id: ID }));
  }, []);

  useEffect(() => {
    setCompany(props.details);
    console.log(props);
    if(props.details.length===0) 
    {
      window.location.href=BASE_URL
    }

    if(props.details.status==0 && props.details.length!==0)
    {
      // window.location.href=BASE_URL
      // alert("Sorry! this company has been In-Active");
      document.body.classList.add('alert-container_open');

      setAlert({
        type: 'error',
        text: 'Podjetje je zaprto/neaktivno. Za ve informacij smo vam na voljo na info@preveri-podjetje.si',
        show: true
      })
    } else {

      setAlert({
        type: '',
        text: '',
        show: false
      })

    }
  }, [props.details, setCompany]);

  function onCloseAlert() {
    setAlert({
      type: '',
      text: '',
      show: false
    });
    document.body.classList.remove('alert-container_open');

    // navigate('/', { replace: true });
    window.location.href=BASE_URL


  }

  return (
    <>
      {company.reg_no && <>
        <Helmet>
        <meta charSet="utf-8" />
        <title>Preveri Podjetje - podjetja Details</title>
        <meta name="description" content="podjetja Details"/>
      </Helmet>
      <Alert
        header={'Opozorilo'}
        btnText={'Zapri'}
        text={alert.text}
        type={alert.type}
        show={alert.show}
        onClosePress={onCloseAlert}
        pressCloseOnOutsideClick={true}
        showBorderBottom={true}
        alertStyles={{}}
        headerStyles={{}}
        textStyles={{}}
        buttonStyles={{}}
      />
        <Banner />
        <CommentModal />
        <Details {...company} />
        <BasicInfo {...company} />

        {
          company.description_status===1 && <> <div className="pictures_sec">
          <div className="container">
            <Row>
              <Col sm="12">
                <Card body>
                  <div className="stats_detail">
                    <CardTitle tag="h1">
                      {t('company.employement')}
                    </CardTitle>
                    <p>{company.description}</p>
                  </div>
                </Card>
              </Col>
            </Row>
          </div>
          </div> </>
          }        

        <Comments {...company} />

        <Link to="/" className="back_login"> {t('company.back_to_company')}</Link>
      </>}
    </>
  )
}

const mapStateToProps = (state: any) => ({
  details: state.companyDetailsStore.details
});

export default connect(mapStateToProps)(CompanyDetails);