import { AnyAction } from 'redux';
import { INIT, LOADING, SUCCESS, ERROR,thanks_spend_time } from '../../helper/constants';
import {
    COMMENTSUBMIT,
    COMMENTSUBMIT_ERROR,
    COMMENTSUBMIT_SUCCESS,
    COMMENTSUBMIT_RESET_ALERT
} from './constants';

interface ICommentSubmit {
    phase: string;
    status: boolean;
    message: string
}

const INIT_STATE: ICommentSubmit = {
    phase: INIT,
    status: false,
    message: ''

}

const commentSubmitStore = (state = INIT_STATE, action: AnyAction) => {
    const { type, payload } = action;
    switch (type) {
        case COMMENTSUBMIT:
            return {
                ...state,
                phase: LOADING
            }
        case COMMENTSUBMIT_RESET_ALERT:
                return {
                    ...state,
                    phase: INIT,
                    message: ''
                }
        case COMMENTSUBMIT_SUCCESS: 
            return {
                ...state,
                phase: SUCCESS,
                status: true,
                error: null,
                message:thanks_spend_time
            }
        case COMMENTSUBMIT_ERROR:
            return {
                ...state,
                status: false,
                phase: ERROR,
                error: payload,
                message: payload
            }
        default:
            return {
                ...state
            }
    }
}

export {
    commentSubmitStore
}
