import { AnyAction } from "redux";
import { call, put, takeLatest } from "redux-saga/effects"
import { COMPANY_API_TYPE } from "../../helper/constants";
import { companyAPI, regionAPI } from "./api";
import { COMPANY, COMPANY_ERROR, COMPANY_SUCCESS, REGION, REGION_ERROR, REGION_SUCCESS } from "./constants"

function* companyList(action: AnyAction): any {
    const { payload } = action;
    const {
        searchType,
        keyword,
        region_id,
        page
    } = payload;
    let params = {};
    switch (searchType) {
        case COMPANY_API_TYPE.SEARCH:
            params = {
                title: keyword,
                page : page
            }
            break;
        case COMPANY_API_TYPE.FILTER:
            params = {
                region_id: region_id,
                title: keyword,
                page : page
            }
            break;
        default:
            break;
    }
    const response = yield call(companyAPI, params);

    if (response.status) {
        yield put({ type: COMPANY_SUCCESS, payload: response.data });
    } else {
        yield put({ type: COMPANY_ERROR, payload: response.message });
    }
}

function* companySaga() {
    yield takeLatest(COMPANY, companyList);
}

function* regionList(): any {
    const response = yield call(regionAPI);

    if (response.status) {
        yield put({ type: REGION_SUCCESS, payload: response.data });
    } else {
        yield put({ type: REGION_ERROR, payload: response.message });
    }
}

function* regionSaga() {
    yield takeLatest(REGION, regionList);
}

export {
    companySaga,
    regionSaga
}