import  { useEffect } from 'react';
import { Pagination, PaginationItem, PaginationLink, Row } from 'reactstrap';
import { Filter } from '../../../components/Company/Filter';
import { Listing } from '../../../components/Company/Listing';
import { ICompanyProps } from "../../../interfaces/ICompanyProps";
import { useDispatch, useSelector } from "react-redux";
import { fetchCompany } from '../../../redux/actions';
import { COMPANY_API_TYPE } from '../../../helper/constants';
import { Helmet } from 'react-helmet';

export default function CompanyList() {
    const companyStore = useSelector((state: any) => state.companyStore)
    const { data, regions, searchType, error, keyword, region_id, current_page, last_page } = companyStore;
    let paginationPages = [];
    if (current_page <= 3) {
        if (last_page >= 5) {
            paginationPages = [1, 2, 3, 4, 5];
        }
        else {
            for (let i = 1; i <= last_page; i++) {
                paginationPages.push(i);
            }
        }
    } else if (current_page >= last_page - 5) {
        for (let i = last_page - 5; i <= last_page; i++) {
            paginationPages.push(i);
        }
    } else {
        for (let i = current_page - 2; i <= current_page + 2; i++) {
            paginationPages.push(i);
        }
    }
    const dispatch = useDispatch();
    useEffect(() => {
        const {searchType } = companyStore;
        
        if (searchType !== COMPANY_API_TYPE.LOAD && !error && data.length === 0 && searchType !== COMPANY_API_TYPE.SEARCH) {
            dispatch(fetchCompany({ searchType: COMPANY_API_TYPE.LOAD }));
            
        } else if (searchType !== COMPANY_API_TYPE.FILTER) {

        }
    }, [data, dispatch, searchType, error]);

    const handleClick = (e:any,pageno:any) => {
        e.preventDefault();
        window.scrollTo(0, 0);

        const { keyword, region_id } = companyStore;

        dispatch(fetchCompany({ searchType: COMPANY_API_TYPE.FILTER,page:pageno,region_id:region_id,keyword:keyword }));
      };

    const filterProps: any = {
        regions,
        searchType,
        region_id,
        keyword
    }
    return (
        <>
          <Helmet>
        <meta charSet="utf-8" />
        <title>Preveri Podjetje - seznam podjetij</title>
        <meta name="description" content="Oglejte si seznam vseh slovenskih podjetij, kjer si lahko ogledate vsako oceno podjetja in tudi delite svoje ocene o svojih delovnih izkunjah v vaem podjetju, ne da bi razkrili svojo identiteto."/>      </Helmet>
            <Filter {...filterProps} />
            <div className="companyListing">
                <div className="container">
                    <Row>
                        {data.map((section: ICompanyProps, index: Number) => <Listing key={`Company${index}`} {...section} />)}
                    </Row>
                </div>
            </div>
            { data.length >1 && 
            <Pagination size="sm" aria-label="Page navigation example">
                {
                    current_page > 1 &&
                    <>
                        <PaginationItem>
                            <PaginationLink first onClick={e => handleClick(e, 1)} />
                        </PaginationItem>
                        <PaginationItem>
                            <PaginationLink previous onClick={e => handleClick(e, current_page - 1)} />
                        </PaginationItem>
                    </>
                }

                {paginationPages.map((i) => {
                    return <PaginationItem active={i === current_page}>
                        <PaginationLink onClick={e => handleClick(e, i)}>
                            {i}
                        </PaginationLink>
                    </PaginationItem>
                })}
                {

                    current_page < last_page &&
                    <>
                        <PaginationItem>
                            <PaginationLink next onClick={e => handleClick(e,current_page + 1)} />
                        </PaginationItem>

                    </>
                }
            </Pagination>
}
        </>
    )
}