import axios from "axios";
import { API_URL, METHODS } from "../../helper/constants";
// import { IRegisterProps } from "../interfaces";

const commentSubmitAPI: any = (data: any) => 
    axios({
        method: METHODS.POST,
        url:  sessionStorage.getItem('token')!==null ? `${API_URL}/comments-add` : `${API_URL}/auth/company-comments-add`,
        headers: { 'Content-Type': 'application/json' },
        data
    });


const pictureSubmitAPI: any = (data: any) => 
    axios({
        method: METHODS.POST,
        url:  sessionStorage.getItem('token')!==null ? `${API_URL}/pictures-add` : `${API_URL}/auth/company-pictures-add`,
        headers: { 'Content-Type': 'application/json' },
        data
    });


export {
    commentSubmitAPI,
    pictureSubmitAPI
}