import { AnyAction } from 'redux';
import { INIT, LOADING, SUCCESS, ERROR } from '../../helper/constants';
import {
    HOME,
    HOME_ERROR,
    HOME_SUCCESS,
} from './constants';

interface IHome {
    alldata: any;
    phase: string;
    message: string;
    homeerror: string;
}

const INIT_STATE: IHome = {
    alldata: [],
    phase: INIT,
    message: '',
    homeerror: ''

}

const homeStore = (state = INIT_STATE, action: AnyAction) => {
    const { type, payload } = action;
    switch (type) {
        case HOME:
            return {
                ...state,
                phase: LOADING
            }
        case HOME_SUCCESS:
            return {
                ...state,
                phase: SUCCESS,
                homeerror: null,
                alldata: payload
            }
        case HOME_ERROR:
            return {
                ...state,
                phase: ERROR,
                homeerror: payload,
                alldata: [],
                message: payload
            }
        default:
            return {
                ...state
            }
    }
}

export {
    homeStore
}
