import { combineReducers } from "redux";
import { contactStore } from "./contact/reducers";
import { companyStore } from "./company/reducers";
import { companyDetailsStore } from "./companyDetails/reducers";
import { newsDetailsStore } from "./newsDetails/reducers";
import { homeStore } from "./home/reducers";
import { newsStore } from "./news/reducers";
import { commentSubmitStore } from "./commentSubmit/reducers"; 
import { pictureSubmitStore } from "./pictureSubmit/reducers"; 

const reducers = combineReducers({
    contactStore,
    companyStore,
    homeStore,
    newsStore,
    commentSubmitStore,
    pictureSubmitStore,
    companyDetailsStore,
    newsDetailsStore,
});

export default reducers;