import { useEffect, useState } from 'react';
import { useParams } from 'react-router';
import { SingleNews } from '../../../components/News/SingleNews';
import { useDispatch, useSelector } from 'react-redux';
import { fetchNewsDetails } from '../../../redux/actions';
import { BASE_URL, SUCCESS } from '../../../helper/constants';
import { Helmet } from 'react-helmet';

export default function NewsDetails(props:any) {
    const newsStore = useSelector((state: any) => state.newsDetailsStore)
    const { details,detailsPhase } = newsStore;

    const dispatch = useDispatch();
    const { id } = useParams();

    const [news, setNews] = useState<any>();

    useEffect(() => {

          dispatch(fetchNewsDetails({ id: id }));
      }, []);

      useEffect(() => {
          if(detailsPhase === SUCCESS)
          {
            setNews(details);
          }

          if(details===null) 
          {
            window.location.href=BASE_URL
          }
      }, [detailsPhase,details, setNews]);


    return (
        <>
         <Helmet>
        <meta charSet="utf-8" />
        <title>Preveri Podjetje - novice details</title>
        <meta name="description" content="novice details"/>
      </Helmet>
        <SingleNews {...news}/>
        </>
    )
}