import { AnyAction } from 'redux';
import { INIT, LOADING, SUCCESS, ERROR } from '../../helper/constants';
import {
    NEWS,
    NEWS_ERROR,
    NEWS_SUCCESS,
} from './constants';

interface ICompany {
    data: any;
    phase: string;
    message: string;
    currentPage: string;
    perPage: number;
    total: number;
    error: string;
}

const INIT_STATE: ICompany = {
    data: [],
    phase: INIT,
    message: '',
    currentPage: '',
    perPage: 0,
    total: 0,
    error: ''

}

const newsStore = (state = INIT_STATE, action: AnyAction) => {
    const { type, payload } = action;
    switch (type) {
        case NEWS:
            return {
                ...state,
                phase: LOADING,
            }
        case NEWS_SUCCESS:
            return {
                ...state,
                phase: SUCCESS,
                error: null,
                data: payload
            }
        case NEWS_ERROR:
            return {
                ...state,
                phase: ERROR,
                error: payload,
                data: [],
                message: payload
            }
        default:
            return {
                ...state
            }
    }
}

export {
    newsStore
}