import { useEffect } from 'react';
import { Footer, Header } from './components';
import { Router } from './router';
import 'bootstrap/dist/css/bootstrap.min.css';
import './assets/style/style.css';
import { useLocation } from "react-router-dom";
import { Loader } from './components/Loader';
import { useDispatch } from 'react-redux';
import { fetchHomeData } from './redux/actions';

function App() {
  const params = useLocation();
  const path = params.pathname.split('/')[1];
  const dispatch = useDispatch();

  var HomePageId;
  switch (path) {
    case 'piskotki':
      HomePageId = 'piskotki';
      break;
    case 'politika-zasebnosti':
      HomePageId = 'politika-zasebnosti';
      break;
    case 'o-nas':
      HomePageId = 'o-nas';
      break;
    case 'pogoji-uporabe':
      HomePageId = 'pogoji-uporabe';
      break;
    case 'podjetja':
      HomePageId = 'podjetja';
      break;
    case 'novice':
      HomePageId = 'novice';
      break;
    default:
      HomePageId = 'home';
      break;
  }

  useEffect(() => {
    dispatch(fetchHomeData());
  }, [])

  return (
    <>

      <div className={HomePageId}>
        <Loader />
        <Header />
        <Router />
        <Footer />
      </div>
    </>
  );
}

export default App;
