import { AnyAction } from "redux";
import { call, put, takeLatest } from "redux-saga/effects"
import { commentSubmitAPI } from "./api";
import { COMMENTSUBMIT, COMMENTSUBMIT_ERROR, COMMENTSUBMIT_SUCCESS } from "./constants"

function* CommentMSubmit(action: AnyAction): any {
    const { payload } = action;
    const response = yield call(commentSubmitAPI, payload);
    if (response.status) {
        yield put({ type: COMMENTSUBMIT_SUCCESS, payload: response.message });     
    } else {
        yield put({ type: COMMENTSUBMIT_ERROR, payload: response.message });     
    }
}

function* commentSubmitSaga() {
    yield takeLatest(COMMENTSUBMIT, CommentMSubmit);
}

export {
    commentSubmitSaga
}