import React, { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import StarRatingComponent from 'react-star-rating-component';
import { Card, Row, Col, CardTitle, CardText, CardImg, Button, Modal, ModalHeader, ModalBody, ModalFooter, FormGroup, Label, Input, FormFeedback } from 'reactstrap';
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import { CommentModal } from "../../Home/CommentModal";
import {  pictureSubmit } from "../../../redux/actions";
import { ALERT_TIMEOUT, COMPANY_URL, STORES, SUCCESS } from "../../../helper/constants";
import { AlertMessage } from "../../AlertMessage";
import { COMPANY_IMAGES_URL } from "../../../helper/constants";
import { FacebookShareButton } from "react-share"
import { useNavigate } from 'react-router';

export default function Details(props: any) {
    const [t] = useTranslation('common');
    const { id } = useParams();
    const dispatch = useDispatch();
    const navigate = useNavigate();

    // use company interface
    const company = props;
    const { category, logo, title, website,rating,total_comments,status } = company;

    const pictureSubmitStore = useSelector((state: any) => state.pictureSubmitStore);

    const [formData, setFormData] = useState<any>({
        company_id: '',
        first_file: File,
        second_file: File,
        third_file: File,
    });

    const [firstFile, setfirstFile] = useState<any>();
    const [secondFile, setsecondFile] = useState<any>();
    const [thirdFile, setthirdFile] = useState<any>();

    const [firstFileError, setfirstFileError] = useState<boolean | null>(null)
    const [secondFileError, setsecondFileError] = useState<boolean | null>(null)
    const [thirdFileError, setthirdFileError] = useState<boolean | null>(null)

    const [DisableUploadButton, setDisableUploadButton] = useState(false);
    const [error, setError] = useState<any>({});

    const [isModalShow, setisModalShow] = useState(false);
    const [isPictureModalShow, setisPictureModalShow] = useState(false);

    const onFileChange = (e: any, type: string) => {

        // Update the state 
        if (type === 'first') {
            setfirstFile(e.target.files[0]);

        } else if (type === 'second') {

            setsecondFile(e.target.files[0]);

        } else {

            setthirdFile(e.target.files[0]);

        }
    };


    const onFileUpload = () => {
        const error: any = {};

        // Create an object of formData 
        const formData = new FormData();
        const { id } = company;
        setDisableUploadButton(true);

        // Update the formData object 
        formData.append("company_id", String(id));
        if (firstFile !== undefined) {
            formData.append("first_file", firstFile);
        }
        if (secondFile !== undefined) {
            formData.append("second_file", secondFile);
        }
        if (thirdFile !== undefined) {
            formData.append("third_file", thirdFile);
        }

        if (firstFile || secondFile || thirdFile) {
            setError({});
            setDisableUploadButton(false);
            dispatch(pictureSubmit(formData));

            setfirstFile('');
            setsecondFile('');
            setthirdFile('');

            setTimeout(() => {
                setisPictureModalShow(false);
            }, ALERT_TIMEOUT);
        } else {
            setDisableUploadButton(false);
            if (!firstFile || !firstFileError) {
                setfirstFileError(false);
                error['first_file'] = t('messages.select_first_file');
            }

            setError(error);
        }

    };




    useEffect(() => {
        if (Object.keys(error).length === 0) {
            setError({});
            setDisableUploadButton(false);
            setisPictureModalShow(false);
        } else {
            setDisableUploadButton(false);
        }


    }, []);

    return (
        <>
            <div className="company_single_main_detail">
                <div className="container">
                    <Row>
                        <Col sm="4">
                            <div className="company_profile_image">
                                <CardImg
                                    alt="Card_image_cap"
                                    src={COMPANY_IMAGES_URL + logo}
                                    width="40%"
                                />
                            </div>
                        </Col>
                        <Col sm="8">
                            <div className="company_profile_data">
                                <CardTitle tag="h1">
                                    {title}
                                </CardTitle>
                                <p className="yellowcolor">{website}</p>
                                <p>{category}</p>
                                <p><StarRatingComponent
                                    name="rate1"
                                    starCount={5}
                                    value={rating}
                                /></p>
                            </div>
                        </Col>
                    </Row>
                </div>
            </div>
            <div className="company_share_comment_sec">
                <div className="container">
                    <Row>
                        <Col sm="6">
                            <FacebookShareButton
                                url={COMPANY_URL+id}
                                quote={title}
                                hashtag={title}
                                >
                                <img
                                alt="Card_image_cap"
                                src="/fb.png"
                                width="40px"
                            />{t('company.share')}
                            </FacebookShareButton>

                        </Col>
                        <Col sm="6">
                            <Button onClick={() => setisModalShow(!isModalShow)}>+ {t('company.comment_or_salary')}</Button>
                            <CommentModal isModalShow={isModalShow} setisModalShow={setisModalShow} source="companyDetails" companyName={title} />
                        </Col>

                    </Row>
                </div>
            </div>
            <div className="picture_other_detail">
                <div className="container">
                    <Row>
                        <Col sm="4">
                            <Card body>

                                <div className="stats_detail">
                                    <CardTitle>
                                        {t('company.comments')}
                                    </CardTitle>
                                    <CardText>
                                        {company.total_comments}
                                    </CardText>
                                </div>
                            </Card>
                        </Col>

                        <Col sm="4">
                            <Card body>

                                <div className="stats_detail"  style={{height: "57px"}}>
                                    <CardTitle>
                                        {t('company.the_payment')}
                                    </CardTitle>
                                   
                                </div>
                            </Card>
                        </Col>
                        <Col sm="4">
                            <Card body>

                                <div className="stats_detail" style={{height: "57px"}}>
                                    <CardTitle>
                                        {t('company.statistics')}
                                    </CardTitle>

                                </div>
                            </Card>
                        </Col>
                    </Row>
                </div>
            </div>
            {/* Picture Modal */}
            <Modal backdrop={false} fade={false} isOpen={isPictureModalShow}>
                <ModalHeader
                    close={<button className="close" onClick={() => setisPictureModalShow(!isPictureModalShow)}></button>}
                    toggle={() => setisPictureModalShow(!isPictureModalShow)}
                >
                   {title}
                </ModalHeader>
                <ModalBody>
                    <AlertMessage store={STORES.pictureSubmitStore} />

                    <FormGroup >
                        <p>
                            Slika 1:
                        </p>

                        <Input
                            name="first_file"
                            type="file"
                            onChange={(e: any) => onFileChange(e, 'first')}
                        />
                    </FormGroup>
                    <FormFeedback invalid={!!error?.first_file}>
                        {error?.first_file}
                    </FormFeedback>
                    <FormGroup >
                        <p>
                            Slika 2:
                        </p>
                        <Input
                            name="second_file"
                            type="file"
                            onChange={(e: any) => onFileChange(e, 'second')}
                        />
                    </FormGroup>

                    <FormGroup >
                        <p>
                            Slika 3:
                        </p>
                        <Input
                            name="third_file"
                            type="file"
                            onChange={(e: any) => onFileChange(e, 'third')}
                        />
                    </FormGroup>

                    <Button onClick={() => onFileUpload()} disabled={DisableUploadButton}>{t('company.upload')} </Button>
                </ModalBody>
                <ModalFooter>

                </ModalFooter>
            </Modal>
        </>
    )
}