import React from 'react';
import { Card, Row, Col, CardTitle, CardText, Button, Pagination, PaginationItem, PaginationLink } from 'reactstrap';
import { useTranslation } from "react-i18next";
import { ICompanySearchProps } from '../../../interfaces/ICompanySearchProps';
import Moment from 'moment';
import { ViewCommentModal } from '../ViewCommentModal';
import { useDispatch } from 'react-redux';
import { fetchCompanyDetails } from '../../../redux/actions';
import { useParams } from 'react-router';
import StarRatingComponent from 'react-star-rating-component';

export default function Comments(props: ICompanySearchProps) {
    const [t] = useTranslation('common');
    const { useState } = React;
    const dispatch = useDispatch();
    const { id } = useParams();

    const Hrstyle = {
        margin: '1em 0',
      };

    const [isModalShow, setisModalShow] = useState<Boolean | Number>(false);

    const {
        comments,
    } = props;

    let paginationPages = [];
    if (comments.current_page <= 3) {
        if (comments.last_page >= 5) {
            paginationPages = [1, 2, 3, 4, 5];
        }
        else {
            for (let i = 1; i <= comments.last_page; i++) {
                paginationPages.push(i);
            }
        }
    } else if (comments.current_page >= comments.last_page - 5) {
        for (let i = comments.last_page - 5; i <= comments.last_page; i++) {
            paginationPages.push(i);
        }
    } else {
        for (let i = comments.current_page - 2; i <= comments.current_page + 2; i++) {
            paginationPages.push(i);
        }
    }

    const handleClick = (e: any, pageno: any) => {
        e.preventDefault();
        dispatch(fetchCompanyDetails({ id: id, page: pageno }));

    }

    return (

        <>
            <div className="comment_sec">
                <div className="container">
                    <Row>
                        <Col sm="12">
                            <Card body>
                                <div className="row">
                                

                                    {
                                        comments.data.map((comment: any, index: Number) => <>
                                        <Col sm="4">
                                            <CardTitle tag="h1">
                                                {t('company.comments')}
                                            </CardTitle>
                                            <CardText key={`Comment${index}`}>
                                                <span>{Moment(comment.created_at).format('MMM DD,YYYY')}</span>
                                                <p>
                                                    <StarRatingComponent
                                                    name="rate1"
                                                    starCount={5}
                                                    value={comment.rating}
                                                />
                                                </p>
                                                </CardText>
                                                </Col>
                                               
                                            
                                                { 
                                                
                                                comment.additional_text === 1 && 
                                                <Col sm="8">
                                                <span className="right-about">Uporabljena je bila cenzura zaradi neupotevanja pravil uporabe portala.<br/>
                                                    (Kletvice, sovrani govor, aljivke, ipd.)
                                                    </span>
                                                    </Col>
                                                }
                                                <Col sm="12">
                                                <h5 className="good_comment">{t('company.good_stuff')}</h5>
                                                <p> {comment.good_stuff}</p>

                                                <h5 className="bad_comment">{t('company.bad_things')}</h5>
                                                <p> {comment.bad_things}</p>

                                                <h5 className="med_comment">{t('company.management_advice')}</h5>
                                                <p> {comment.management_advice}</p>
                                                <Button onClick={() => setisModalShow(index)}> {t('company.view_comment')}</Button>
                                                <ViewCommentModal isModalShow={isModalShow === index} setisModalShow={setisModalShow} source={comment.id} fullResponse={comment.full_response} />
                                                </Col>
                                                <hr style={Hrstyle} />
                                          
                                        </>
                                        )
                                    }

                                    {
                                        comments.data.length === 0 ? <>
                                            <CardTitle tag="h1">
                                                {t('company.comments')}
                                            </CardTitle>{t('messages.no_comment_found')}
                                        </> : ''
                                    }

                                    {
                                        comments.data.length !== 0 &&
                                        <Pagination size="sm" aria-label="Page navigation example">
                                            {
                                                comments.current_page > 1 &&
                                                <>
                                                    <PaginationItem>
                                                        <PaginationLink first onClick={e => handleClick(e, 1)} />
                                                    </PaginationItem>
                                                    <PaginationItem>
                                                        <PaginationLink previous onClick={e => handleClick(e, comments.current_page - 1)} />
                                                    </PaginationItem>
                                                </>
                                            }

                                            {paginationPages.map((i) => {
                                                return <PaginationItem active={i === comments.current_page}>
                                                    <PaginationLink onClick={e => handleClick(e, i)}>
                                                        {i}
                                                    </PaginationLink>
                                                </PaginationItem>
                                            })}
                                            {

                                                comments.current_page < comments.last_page &&
                                                <>
                                                    <PaginationItem>
                                                        <PaginationLink next onClick={e => handleClick(e, comments.current_page + 1)} />
                                                    </PaginationItem>
                                                </>
                                            }
                                        </Pagination>
                                    }
                                </div>
                            </Card>
                        </Col>
                    </Row>
                </div>
            </div>
        </>
    )
}
