import StarRatingComponent from 'react-star-rating-component';
import { Card, CardImg, CardText, CardTitle } from 'reactstrap';
import { IRatingProps } from '../../../interfaces/IRatingProps';
import { COMPANY_IMAGES_URL } from '../../../helper/constants';
import { Link } from 'react-router-dom';
import { useTranslation } from "react-i18next";

export default function Rating(props: IRatingProps) {
    const [t] = useTranslation('common');

    const {
        type,
        companyImage,
        companyName,
        comment,
        rating,
        slug,
        baseRating,
    } = props;
    return (

        <Card body>
            {
                companyName === undefined ? (<>

                    <div className="review_image">
                        <CardImg
                            alt={companyName}
                            src={COMPANY_IMAGES_URL + 'no_image.png'}
                            top
                            width="20%"
                        />
                    </div>
                    <div className="review_detail">
                        <CardTitle tag="h5">
                            {type}
                        </CardTitle>
                        <CardText>
                        <div className="comment">{t('messages.no_comment_found')}</div>
                    </CardText>
                    </div>
                   
                </>) : (<>
                    <div className="review_image">
                        <CardImg
                            alt={companyName}
                            src={COMPANY_IMAGES_URL + companyImage}
                            top
                            width="20%"
                        />
                    </div>
                    <div className="review_detail">
                        <CardTitle tag="h5">
                            {type}
                        </CardTitle>

                        {
                            comment &&
                            <CardText>
                                <div className="comment">
                                </div>
                            </CardText>
                        }
                        <CardText>
                            <Link to={`/podjetja/${slug}`}>{companyName} </Link>
                            <StarRatingComponent
                                name="rate1"
                                starCount={baseRating}
                                value={rating}
                            />
                        </CardText>
                    </div>
                </>
                )}
        </Card>
    )
}