import { COMPANY, COMPANY_RESET_ALERT, REGION } from "./constants";
import { ICompanyProps } from "../interfaces";

const fetchCompany = (payload: ICompanyProps) => ({
    type: COMPANY,
    payload
});

const resetCompanyAlert = () => ({
    type: COMPANY_RESET_ALERT
});

const fetchRegion = () => ({
    type: REGION,
});


export {
    fetchCompany,
    resetCompanyAlert,
    fetchRegion,
}