import { all } from "redux-saga/effects";
import { contactSaga } from "./contact/saga";
import { companySaga, regionSaga } from "./company/saga";
import { companyDetailsSaga } from "./companyDetails/saga";
import { newsDetailsSaga } from "./newsDetails/saga";
import { newsSaga } from "./news/saga";
import { homeSaga } from "./home/saga";
import { commentSubmitSaga } from "./commentSubmit/saga";
import { pictureSubmitSaga } from "./pictureSubmit/saga";

export default function* rootSaga() {
    yield all([
        contactSaga(),
        companySaga(),
        regionSaga(),
        newsSaga(),
        homeSaga(),
        commentSubmitSaga(),
        pictureSubmitSaga(),
        companyDetailsSaga(),
        newsDetailsSaga(),
    ]);
}