import axios from "axios";
import { API_URL, METHODS } from "../../helper/constants";

const homeAPI: any = (params: any) =>
    axios({
        method: METHODS.GET,
        url: `${API_URL}/auth/home-page-section`,
        headers: { 'Content-Type': 'application/json' },
        params
    });
    
export {
    homeAPI
}