import  { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Alert } from "reactstrap"
import { ALERT_CLASS, ALERT_TIMEOUT, ERROR, SUCCESS, STORES } from "../../helper/constants";
import { resetCompanyAlert,resetCommentAlert, resetPictureAlert, resetContactAlert } from "../../redux/actions";

export default function AlertMessage(props: any) {

  const { loginStore, registerStore,forgotStore,contactStore,profileStore, companyStore,commentSubmitStore,pictureSubmitStore } = useSelector((state: any) => state);
  const { store }: {store: STORES} = props;
  const stores = {
    [STORES.COMPANY_STORE]: companyStore,
    [STORES.LOGIN_STORE]: loginStore,
    [STORES.REGISTER_STORE]: registerStore,
    [STORES.FORGOT_STORE]: forgotStore,
    [STORES.PROFILE_STORE]: profileStore,
    [STORES.CONTACT_STORE]: contactStore,
    [STORES.commentSubmitStore]: commentSubmitStore,
    [STORES.pictureSubmitStore]: pictureSubmitStore,

  };
  const dispatch = useDispatch();
  const message: boolean = stores[store].message;
  const phase: string = stores[store].phase;
  const { SUCCESS: ALERT_SUCCESS, ERROR: ALERT_ERROR } = ALERT_CLASS;

  useEffect(() => {
    if ([SUCCESS, ERROR].find(status => status === phase)) {

      setTimeout(() => {
        switch (store) {
          case STORES.COMPANY_STORE:
            dispatch(resetCompanyAlert());
            break;
         
          case STORES.commentSubmitStore:
            dispatch(resetCommentAlert());
            break;
          case STORES.pictureSubmitStore:
              dispatch(resetPictureAlert());
              break;
          case STORES.CONTACT_STORE:
            dispatch(resetContactAlert());
            break;
          default:
            break;
        }
      }, ALERT_TIMEOUT);
    }
  }, [dispatch, message, phase, store]);


  return (
    <>
      {
        <Alert color={phase === SUCCESS ? ALERT_SUCCESS : ALERT_ERROR} isOpen={!!message}>
          {message}
        </Alert>
      }
    </>
  )
}
