import { Routes, Route ,Navigate} from 'react-router-dom';
import { Home,Privacy,About,Terms,CompanyList,CompanyDetails } from '../containers';
import { NewsList } from '../containers';
import { Policy } from '../containers';
import { NewsDetails } from '../containers/News/NewsDetails';
export default function Router() {
    
    return (
        <Routes>
            <Route path='/' element={<Home />} />
            <Route path='/:id' element={<Home />} />
            <Route path='/piskotki' element={<Policy />} />
            <Route path='/politika-zasebnosti' element={<Privacy />} />
            <Route path='/o-nas' element={<About />} />
            <Route path='/pogoji-uporabe' element={<Terms />} />
            <Route path='/podjetja' element={<CompanyList />} />
            <Route path='/podjetja/:id' element={<CompanyDetails />} />
            <Route path='/novice' element={<NewsList />} />
            <Route path='/novice/:id' element={<NewsDetails />} />
            <Route path="*" element={<Navigate to ="/" />}/>

        </Routes>
    )
}